import { BrowserRouter, Routes, Route } from 'react-router-dom';
import './App.css';
import 'bootstrap-icons/font/bootstrap-icons.css';

import './Component/Board-Home/Banner-cta.css';
import './Component/Newest-recipe-sect/New-recipe-section.css';
import './Component/Nav-comp/Bottom-nav/Bottom-nav.css';
import './Component/Main-cta-home/Cta-home-main.css';
import './Component/Resep-pilihan/ResepPilihan.css';
import './Component/1.Btm-nav-comp/Explore/Explore-comp.css';
import './Component/SavedContent/SavedSect.css'
import './Component/Notes/NotesPage.css'

import 'react-loading-skeleton/dist/skeleton.css'


import Home from './Component/Home';
import Setting from './Component/Nav-comp/Setting-sect/Setting';
import ExploreComp from './Component/1.Btm-nav-comp/Explore/Explore-comp';
import SavedNav from './Component/SavedContent/SavedSect'
import Error404 from './Component/Error404/Error404';
import NotesPage from './Component/Notes/NotesPage';

import NotesContextProvider from './Component/Notes/NoteContext';

// Context Darkmode
import DarkModeProvider from './DarkMode/Darkmode';
// import { DarkModeContext } from './DarkMode/Darkmode';

import { useEffect, useState, } from 'react';

// !Offline Indicator
import OfflineIndicator from './Popup-Component/Offline-indicator/Offline-indicator';
// Feedback
import FeedbackProvider from './Popup-Component/Feedback-Popup/FeedbackContext';
// Send Save menu to Saved.page
import SaveMenuProvider from './Component/Newest-recipe-sect/Content-props/SendSaveContext';
import { InputSearchProvider } from './Component/Nav-comp/Top-nav/SendRef';
import Params from './NewPageCozDataMap/PageDataMapFromInput/ParamsInputData';
import ParamProvider from './NewPageCozDataMap/paramsContext';
import ParamsMenuScroll from './NewPageCozDataMap/PageDataMenuScroll/ParamsMenuScroll';

import ParamsProviderSaveContext from './NewPageCozDataMap/PageDataMenuScroll/ContextSaveMenu';
// import Hotjar from '@hotjar/browser';
// const siteId = 5041753;
// const hotjarVersion = 6;

// Hotjar.init(siteId, hotjarVersion);

export default function App() {
  // window.addEventListener('scroll', () => {
  //   console.log(window.scrollY);
  // });

  // !Online offline Indicator
  const [WebOnline, setWebOnline] = useState(navigator.onLine)

  function HandleConnectionOnline() {
      setWebOnline(navigator.onLine)
      window.location.reload();
  }

  function HandleConnectionOffline() {
    setWebOnline(navigator.onLine)
  }

  useEffect(()=> {
    window.addEventListener('online', HandleConnectionOnline)
    window.addEventListener('offline', HandleConnectionOffline)
  }, [])

 

  return (
    <div className='App'>

      {/* Offline Indicator */}
      {!WebOnline && (
          <OfflineIndicator />
      )}

      <DarkModeProvider> {/* Darkmode context */}
          <FeedbackProvider> {/* Feedback context*/}
            <SaveMenuProvider> {/* Save menu context */}
              <InputSearchProvider> {/* Direct home input search into explore and active */}
                <NotesContextProvider> {/* Notes context*/}
                  <ParamProvider>
                    <ParamsProviderSaveContext> {/* Save menu from Params Menu scroll */}
                      <BrowserRouter>
                        <Routes>
                          <Route index element={<Home />} />
                          <Route path='/Home' element={<Home />} />
                          <Route path='/Settings' element={<Setting />} />

                          {/* Bottom nav routes */}
                          <Route path='/Explore' element={<ExploreComp />} />
                          <Route path='/Explore/Search/:id' element={<Params />} />
                          <Route path='/Explore/Menu/:id' element={<ParamsMenuScroll />} />
                          <Route path='/Saved' element={<SavedNav />} />
                          <Route path='/Notes' element={<NotesPage />} />

                          {/* Error 404 */}
                          <Route path='*' element={<Error404 />} />
                        </Routes>
                      </BrowserRouter>
                    </ParamsProviderSaveContext>
                  </ParamProvider>
                </NotesContextProvider>
              </InputSearchProvider>
            </SaveMenuProvider>
          </FeedbackProvider>
      </DarkModeProvider>
    </div>
  );
}
