import { useEffect, useState, useContext, useRef } from "react";
import { NotesContext } from "./NoteContext";
import BottomNav from "../Nav-comp/Bottom-nav/Bottom-nav"
import LoadingPage from "../Loading-page/Loadcomp";
import { InputContext } from "../Nav-comp/Top-nav/SendRef";
import { DarkModeContext } from "../../DarkMode/Darkmode";

export default function NotesPage() {
  // Darkmode Toggle
  const { modeToggle, setModeToggle } = useContext(DarkModeContext)
    // Loading page
  const [load, setLoad] = useState(false);
  useEffect(() => {
      setTimeout(() => {
        setLoad(true);
      }, 200);
  }, []);

  // Blur input in explore
  const {setStateInputRef} = useContext(InputContext)
  useEffect(()=> {
    setStateInputRef(false)
  }, [setStateInputRef])

  const StyleNotes = {
    backgroundColor: modeToggle ? "black" : "white", height: "fit-content", width: "100%",
  }

  // const {NullNotes, setNullNotes} = useContext(NotesContext)

  const [keyboardActive, setKeyboardActive] = useState(false)
  function KeyboardActive() {
    if(comingPopup || NotePopupRight) {
      setKeyboardActive(true)
    }
  }
  function KeyboardBlur() {
    setKeyboardActive(false)
  }

  const BoxPopupNote = {
    position: "absolute",
    top: keyboardActive ? "20%" : "30%",
    transition: "0.5s ease",
    width: "260px",
    height: "fit-content",
    backgroundColor: "var(--Ground-White)",
    borderRadius: "16px",
    padding: "16px",
    display: "flex",
    flexDirection: "column",
  }

  const BoxOutputNote = {
    position: "absolute",
    transition: "0.5s ease",
    width: "260px",
    height: "fit-content",
    backgroundColor: "var(--Ground-White)",
    padding: "16px",
    display: "flex",
    borderRadius: "12px",

    flexDirection: "column",
    color: modeToggle ? "white" : "black"
  }

  // Note form
  // const [ActiveOutput, setActiveOutput] = useState(false)

  // Popup note form
  const [comingPopup, setComingPopup] = useState(false)
  useEffect(()=> {
    comingPopup ? document.body.style.overflow="hidden" : document.body.style.overflow="auto"
  })

  const [judulInput, setJudulInput] = useState(null)
  const [isiInput, setIsiInput] = useState(null)
  const [arrayNote, setArrayNote] = useState(()=> {
    const savedNote = localStorage.getItem('notes')
    return savedNote ? JSON.parse(savedNote) : [];
  })

  function HandleClickNote() {
    // Show output 
    // setActiveOutput(true)

    if(!judulInput || judulInput === ' ') {
      alert('Judul / isi tidak boleh kosong')
      return
    } else if (!isiInput || isiInput === ' '){
      alert('Judul / isi tidak boleh kosong')
      return
    }
    setComingPopup(false)
    // setNullNotes(false)
    setSwitcherPopupNote(true)

    const ArrayOutput = {
      judul: judulInput,
      isi: isiInput,
      // gambar: imagePreview,
    }
    setArrayNote([...arrayNote, ArrayOutput])
    setJudulInput('');
    setIsiInput('');
  }
  
  // Image note
  // const [imagePreview, setImagePreview] = useState(null);
  // const [nameFileNote, setNameFileNote] = useState(false)
  // useEffect(() => {
  //   // Cek apakah ada data gambar yang disimpan di localStorage
  //   const storedImage = localStorage.getItem('storedImage');
  //   if (storedImage) {
  //       setImagePreview(storedImage);
  //   } 
  // }, []);

  // const handleImageChange = (e) => {
  //   const file = e.target.files[0];
  //   setNameFileNote(file.name)

  //   if (file) {
  //       const reader = new FileReader();
  //       reader.onloadend = () => {
  //           setImagePreview(reader.result);
  //           // localStorage.setItem('storedImage', reader.result);
  //       };
  //       reader.readAsDataURL(file);
  //   }
  // };
  // Load notes from local storage when component mounts
  useEffect(() => {
    const storedNotes = localStorage.getItem('notes');
    if (storedNotes) {
      setArrayNote(JSON.parse(storedNotes));
    }
  }, []);

  // Save notes to local storage when notes state changes
  useEffect(() => {
    localStorage.setItem('notes', JSON.stringify(arrayNote));
  }, [arrayNote]);

  // Note Right Side 
  const [NotePopupRight, setNotePopupRight] = useState(false)

  const [rightJudulNote, setRightJudulNote] = useState(null)
  const [rightIsiNote, setRightIsiNote] = useState(null)

  const [arrayNoteRight, setArrayNoteRight] = useState(()=> {
    const arrayNoteRightState = localStorage.getItem('NotesRight')
    return arrayNoteRightState ? JSON.parse(arrayNoteRightState) : []
  })

  function HandleClickRightNote() {
    if(!rightJudulNote || rightJudulNote === ' ') {
      alert('Judul / isi tidak boleh kosong')
      setRightJudulNote('')
      return
    } else if (!rightIsiNote || rightIsiNote === ' '){
      alert('Judul / isi tidak boleh kosong')
      setRightIsiNote('')
      return
    }

    const arrayNoteRightValue = {
      judulRight: rightJudulNote,
      isiRight: rightIsiNote
    }
    setNotePopupRight(false)
    // setNullNotes(false)
    setSwitcherPopupNote(false)


    setArrayNoteRight([...arrayNoteRight, arrayNoteRightValue])
    setRightJudulNote('')
    setRightIsiNote('')
    // window.location.reload()

  }

  useEffect(()=> {
    localStorage.setItem('NotesRight',JSON.stringify(arrayNoteRight))
  }, [arrayNoteRight])

  const [SwitcherPopupNote, setSwitcherPopupNote]= useState(()=> {
    const SwitcherLocalNote = localStorage.getItem('SwitcherNoteState')
    return SwitcherLocalNote === "true"
  })

  // Handle Change state
  function HandleClickToSwitch() {
    if (SwitcherPopupNote) {
        setComingPopup(false);
        setNotePopupRight(true)
    } else {
      setComingPopup(true);
      setNotePopupRight(false);
    }
    setSwitcherPopupNote(prev => !prev)
  }

  useEffect(()=> {
    const GetSwitcherNote = localStorage.setItem('SwitcherNoteState', SwitcherPopupNote)
    if (GetSwitcherNote) {
      setSwitcherPopupNote(GetSwitcherNote)
    }
  }, [SwitcherPopupNote])

  // User onClick Note content Left
  const [showContentNote, setShowContentNote] = useState(false)
  const [selectedNote, setSelectedNote] = useState(null)

  
  // open popup note func
  function HandleClickContent(index) {
    setShowContentNote(true)
    setSelectedNote(index)
  }

  // Delete func
  function DelFuncLeft(index) {
    setArrayNote(arrayNote => arrayNote.filter((_,i) => i !== index))
    setSwitcherPopupNote(false)
  }

  // Copy func
  const [afterCopy, setAfterCopy] = useState(false)
  const HandleCopy = async (Judul, Isi) => {
    const textToCopy = `${Judul}\n${Isi}.\n\nMy note in Cookityo.app`;
    setAfterCopy(true)
    setTimeout(()=> {
      setAfterCopy(false)
    }, 2000)
    try {
      await navigator.clipboard.writeText(textToCopy);
    } catch (err) {
      console.error('Failed to copy text: ', err);
    }
  };
    
  const ContentNote = () => {
    const SelectedNote = arrayNote[selectedNote]

    return(
      <div style={{ color: "black"}}>
          <div style={{display: "flex", flexDirection: "column", }}>
              {/* <div style={{width: "100%", height: "150px", marginBottom: "12px"}}>
                <img src={SelectedNote.gambar} alt="" width={"100%"} height={"100%"} style={{ objectFit: "cover", borderRadius: "8px", }} />
              </div> */}
            <p style={{fontSize: "16px", fontFamily: "inter", fontWeight: "600"}}>{SelectedNote.judul}</p>
            <p style={{fontSize:"12px", fontWeight: "400", whiteSpace: "pre-wrap"}}>{SelectedNote.isi}</p>
          </div>
          
          <div onClick={()=> HandleCopy(SelectedNote.judul, SelectedNote.isi)} style={{display: "flex", gap: "4px", height: "100%", alignItems: "center", color: "#999999", fontFamily: "inter", cursor: "pointer", userSelect: "none", marginTop: "12px", lineHeight:"14px"}}>
              {afterCopy ? (
                <>
                  <i style={{fontSize: "13px"}} class="bi bi-check-lg"></i>
                  <p style={{fontSize: "10px", fontWeight: "500"}}>Tersalin!</p>
                </>
              ) : (
                <>
                  <i style={{fontSize: "11px"}} class="fa-regular fa-clone"></i>
                  <p style={{fontSize: "10px", fontWeight: "500"}}>Salin</p>
                </>
              )}
          </div>
        </div>
    )
  }

  // User onClick Note Content Right
  const [showContentRightNote, setShowContentRightNote] = useState(false)
  const [selectedNoteRight, setSelectedNoteRight] = useState()

  function HandleClickContentRightNote(index) {
    setShowContentRightNote(true)
    setSelectedNoteRight(index)
  }

  function DelFuncRight(index) {
    setArrayNoteRight(arrayNoteRight => arrayNoteRight.filter((_, i) => i !== index))
    setSwitcherPopupNote(true)
  }

  // Option note
  const [ShowOption, setShowOption] = useState(false)

   const ContentNoteRight = () => {
    const SelectedNoteRight = arrayNoteRight[selectedNoteRight]
    return (
      <div style={{ color: "black",}}>
          <div style={{display: "flex", flexDirection: "column", }} key={arrayNoteRight.id}>
            {/* <div style={{width: "100%", height: "150px", marginBottom: "12px"}}>
              <img src={SelectedNoteRight.gambar} alt="" width={"100%"} height={"100%"} style={{ objectFit: "cover", borderRadius: "8px", }} />
            </div> */}
            <p style={{fontSize: "16px", fontFamily: "inter", fontWeight: "600"}}>{SelectedNoteRight.judulRight}</p>
            <p style={{fontSize:"12px", fontWeight: "400", whiteSpace: "pre-wrap"}}>{SelectedNoteRight.isiRight}</p>
          </div>
          <div onClick={()=> HandleCopy(SelectedNoteRight.judulRight, SelectedNoteRight.isiRight)} style={{display: "flex", gap: "4px", height: "100%", alignItems: "center", color: "#999999", fontFamily: "inter", cursor: "pointer", userSelect: "none", marginTop: "12px", lineHeight:"14px"}}>
              {afterCopy ? (
                <>
                  <i style={{fontSize: "13px"}} class="bi bi-check-lg"></i>
                  <p style={{fontSize: "10px", fontWeight: "500"}}>Tersalin!</p>
                </>
              ) : (
                <>
                  <i style={{fontSize: "10px"}} class="fa-regular fa-clone"></i>
                  <p style={{fontSize: "10px", fontWeight: "500"}}>Salin</p>
                </>
              )}
          </div>
      </div>
    )
   }


   const [NoNote, setNoNote] = useState(true)
   useEffect(()=> {
    if(arrayNote.length > 0 || arrayNoteRight.length > 0) {
      setNoNote(false)
    } else {
      setNoNote(true)
    }
   }, [arrayNote, arrayNoteRight])


  // Style
  const NoteContentElipsis = {
    fontSize: "11px",
    fontFamily: "inter",
    fontWeight: "400",
    display: "-webkit-box",
    WebkitBoxOrient: "vertical",
    webkitLineClamp: 2,
    overflow: "hidden",
    textOverflow: "ellipsis",
    width: "100%",
    color: modeToggle ? "var(--Inner-Input)" : "#666666",
    whiteSpace: "pre-wrap"
  }
  const NoteJudulElipsis = {
    fontSize: "13px",
    fontFamily: "inter",
    marginBottom: "4px",
    whiteSpace: "nowrap",
    fontWeight: "600",
    width: "100%",
    overflow: "hidden",
    textOverflow: "ellipsis", 
    color: modeToggle ? "white" : "black",
  }
  const NameFileImageNote = {
  fontSize: "10px",
    fontFamily: "inter",
    fontWeight: "400",
    whiteSpace: "nowrap",
    width: "100%",
    overflow: "hidden",
    textOverflow: "ellipsis", 
  }
  const SideNote = {
    display: "flex",
    flexDirection: "column",
    width: "160px",
    height: "fit-content",
  }


  // Fungsi keyboard not to block the input

//  useEffect(()=> {
//   setModeToggle(true)
//  }, [setModeToggle])

  
  
  return (
      <div style={{backgroundColor: modeToggle ? "black" : "white", width: "100%", height: "100vh"}} >
        {load ? (
          
          <div style={StyleNotes}>
            {/* Header */}
            <div style={{display: "flex", flexDirection: 'row', alignItems: "center", borderBottom: modeToggle ? '1px solid #292929' : '0px',}}>
              <div style={{color: modeToggle ? "white" : "black", padding: "16px 16px 8px 16px", height: "56px", width: "100%", display: "flex", alignItems: "center", justifyContent: "space-between"}}>
                <p style={{fontSize: "14px", fontFamily: "Main-Bold"}}>Catatan</p>

                <div style={{cursor: "pointer"}} onClick={HandleClickToSwitch}>
                  <i style={{fontSize: "24px", color: modeToggle ? "white" : "black"}} class="bi bi-plus"></i>
                </div>

              </div>
          </div>

            {/* Content note*/}
            <div style={{width: "100%", height: "100%", color: modeToggle ? "white" : "black", display: "flex",  flexDirection: "column", padding: modeToggle ? "16px 16px 70px 16px" : "0px 16px 70px 16px", gap: "12px"}}>

              {NoNote && (
                <div style={{display: "flex", width:"100%", height: "80vh", justifyContent: "center", alignItems: "center", color: modeToggle ? "#999999" : "var(--SubHeading-shade)", fontSize: "12px", fontFamily: "inter"}}>
                  <p>Tidak ada catatan</p>
                </div>
              )}
            

              {/* Popup Notes form Left*/}
              {comingPopup && (
                <div className="Comingsoon-continer" style={{color: "black"}}>
                  <div className="black-background" onClick={()=> {setComingPopup(false); setSwitcherPopupNote(false)}} />
                    <div style={BoxPopupNote}>
                      <div style={{marginBottom: "8px", width: "100%"}}>
                        <input type="search" style={{width: "100%"}} onChange={(e)=> {setJudulInput(e.target.value); }} placeholder="Judul" className="InputNoteJudul" autoFocus onFocus={KeyboardActive} onBlur={KeyboardBlur} />
                        {/* localStorage.setItem('JudulNote', e.target.value) */}
                      </div>

                      <div style={{marginBottom: "8px", width: "100%"}}>
                        <textarea name="" id="" style={{width: "100%"}} onChange={(e)=> {setIsiInput(e.target.value); }} placeholder="Catatan kamu" className="InputNoteIsi" onFocus={KeyboardActive} onBlur={KeyboardBlur}/>
                        {/* localStorage.setItem("IsiNote", e.target.value) */}
                      </div>

                      {/* <div style={{marginBottom: "8px", width: "100%"}}>
                        <input type="file" name="" id="image" accept="image/*" onChange={handleImageChange} style={{display: "none"}}/>
                        <label htmlFor="image" className="InputImageNote" onFocus={KeyboardActive} onBlur={KeyboardBlur} ><i class="fa-solid fa-plus"></i> Add image</label>
                        {nameFileNote && (
                          <div style={NameFileImageNote}>{nameFileNote}</div>
                        )}
                      </div> */}

                      <button onClick={HandleClickNote} className="SubmitNote">Tambah</button>
                  </div>
                </div>
              )}

              {/* Popup Notes form Right */}
              {NotePopupRight && (
                <div className="Comingsoon-continer" style={{color: "black"}}>

                  <div className="black-background" onClick={()=> {setNotePopupRight(false); setSwitcherPopupNote(true)}} />
                    <div style={BoxPopupNote}>
                      <div style={{marginBottom: "8px", width: "100%"}}>
                        <input type="search" style={{width: "100%"}} onChange={(e)=> {setRightJudulNote(e.target.value); }} placeholder="Judul" className="InputNoteJudul" autoFocus onFocus={KeyboardActive} onBlur={KeyboardBlur} />
                        {/* localStorage.setItem('RightJudulNote', e.target.value) */}
                      </div>

                      <div style={{marginBottom: "8px", width: "100%"}}>
                        <textarea name="" id="" style={{width: "100%",}} onChange={(e)=> {setRightIsiNote(e.target.value);}} placeholder="Catatan kamu" className="InputNoteIsi" onFocus={KeyboardActive} onBlur={KeyboardBlur} />
                        {/* localStorage.setItem("RightIsiNote", e.target.value) */}
                      </div>

                      {/* <div style={{marginBottom: "8px", width: "100%"}}>
                        <input type="file" name="" id="image" accept="image/*" onChange={HandleClickRightNote} style={{display: "none"}}/>
                        <label htmlFor="image" className="InputImageNote" onFocus={KeyboardActive} onBlur={KeyboardBlur}><i class="fa-solid fa-plus"></i> Add image</label>
                        {nameFileNote && (
                          <div style={NameFileImageNote}>{nameFileNote}</div>
                        )}
                      </div> */}

                      <button onClick={HandleClickRightNote}className="SubmitNote" >Tambah</button>
                  </div>
                </div>
              )}

              {/* No note  */}
              {/* {NullNotes && (
                <>
                <div style={{width: "100%", height: "80vh", color: modeToggle ? "white" : "black", display: "flex", alignItems: "center", justifyContent: "center", flexDirection: "column", position: "relative"}}>

                  <div onClick={()=> setComingPopup(true)} style={{display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center",}}>
                    <p style={{fontFamily: "Main-Bold", fontSize: "14px"}}>Kamu belum punya catatan</p>
                    <button style={{marginTop: "2px", borderRadius: "4px", border: "none", outline: "none", fontSize: "10px"  , textDecoration: "underline",  fontFamily:"inter", backgroundColor: "transparent", fontWeight: "500", color: modeToggle ? "#999999" : "var(--SubHeading-shade)", cursor: "pointer"}}> Buat catatan</button>
                  </div>

                </div>
                </>
              )} */}

              {/* Output note */}

                

              {/* {ActiveOutput && ( */}
              <div style={{display: "flex", gap: "8px", justifyContent: "space-between", width: '100%', flexWrap: "wrap",}}>
                {/* Left side */}
                <div style={SideNote}>
                  <div style={{gap: "8px", width: "100%", height: "100%", display: "flex", flexDirection: "column",}}>
                    {arrayNote.map((output, index)=> (
                      <>
                        <div style={{width: "160px", height: "fit-content", borderRadius: "8px",backgroundColor: modeToggle ? "rgb(41, 41, 41)" : "#F2F4F6", display: "flex", flexDirection: "column", padding: "12px", justifyContent: "space-between"}} onClick={()=> HandleClickContent(index)}>
                          <div style={{display: "flex", flexDirection: "column"}}>
                              <span style={NoteJudulElipsis}>{output.judul}</span>
                              <span style={NoteContentElipsis}>{output.isi}</span>
                          </div>
                          <span style={{display: "flex", alignItems: "center", justifyContent: "start", padding: "12px 0px 0px 0px", width: "fit-content", cursor: "pointer"}} onClick={(e)=> {e.stopPropagation(); DelFuncLeft(index)}}>
                            <i class="fa-solid fa-square-minus" style={{color: "#ff6347", fontSize: "10px"}} title="Delete note"></i>
                            <p style={{fontSize: "10px", marginLeft: "4px", fontWeight: "600", color: "#ff6347", fontFamily: "inter"}}>Delete</p>
                          </span>
                        </div>

                      
                      </>
                    ))}
                  </div>
                </div>

                {/* Right side */}
                <div style={SideNote}>
                  <div style={{gap: "8px", width: "100%", height: "100%", display: "flex", flexDirection: "column", }}>
                  {arrayNoteRight.map((outputRight, index)=> (
                    <>
                      <div key={index} style={{width: "160px", height: "fit-content", borderRadius: "8px",backgroundColor: modeToggle ? "rgb(41, 41, 41)" : "#F2F4F6", display: "flex", flexDirection: "column", padding: "12px", justifyContent: "space-between"}} onClick={()=> HandleClickContentRightNote(index)}>
                        <div style={{display: "flex", flexDirection: "column"}}>
                              <span style={NoteJudulElipsis}>{outputRight.judulRight}</span>
                              <span style={NoteContentElipsis}>{outputRight.isiRight}</span>
                        </div>
                        <span style={{display: "flex", alignItems: "center", justifyContent: "start", padding: "12px 0px 0px 0px", width: "fit-content", cursor: "pointer" }} onClick={(e)=> {e.stopPropagation(); DelFuncRight(index)}}>
                          <i class="fa-solid fa-square-minus" style={{color: "#ff6347", fontSize: "10px"}} title="Delete note"></i>
                          <p style={{fontSize: "10px", marginLeft: "4px", fontWeight: "600", color: "#ff6347", fontFamily: "inter"}}>Delete</p>
                        </span>
                      </div>
                    </>
                    ))}
                  </div>
                </div>


              </div>

              
              {/* Popup Content note user */}
              {showContentNote && (
                <>
                  <div className="Comingsoon-continer">
                    <div className="black-background" onClick={()=> {setShowContentNote(false); setAfterCopy(false)}} />
                      <div style={BoxOutputNote}>
                        <ContentNote /> 
                      </div>
                  </div>
                </>
              )}

              {showContentRightNote && (
                <>
                <div className="Comingsoon-continer">
                    <div className="black-background" onClick={()=> {setShowContentRightNote(false); setAfterCopy(false)}} />
                      <div style={BoxOutputNote}>
                      <ContentNoteRight />
                      </div>
                  </div>
              </>

              )}



            </div>
          </div>
        ) : (
            <LoadingPage DarkMode={modeToggle} SavedHeight={{height: "100vh"}}/>
        )}
          {modeToggle ? (
            <BottomNav DarkMode={modeToggle} />
          ) : (
            <BottomNav />
          )}
      </div>
    )
}

