import { useContext, useState, useEffect } from "react";
import BottomNav from "../Nav-comp/Bottom-nav/Bottom-nav";
// save Context
import { SaveContext } from "../Newest-recipe-sect/Content-props/SendSaveContext";
import  "../Newest-recipe-sect/New-recipe-section.css";

// Darkmode Context
import { DarkModeContext } from "../../DarkMode/Darkmode";

// Loading 
import LoadingPage from "../Loading-page/Loadcomp";
import ContentNewSect from "../Newest-recipe-sect/Content-props/Content-props";

import { InputContext } from "../Nav-comp/Top-nav/SendRef";
import { useNavigate } from "react-router-dom";

// Menu from ParamsMenuScroll
import ParamsMenuScroll from "../../NewPageCozDataMap/PageDataMenuScroll/ParamsMenuScroll";
import { ParamsSavedMenuContext } from "../../NewPageCozDataMap/PageDataMenuScroll/ContextSaveMenu";
import RecipeScrollContent from "../Recipe-scroll/RecipeScrollContent";
import { ParamContext } from "../../NewPageCozDataMap/paramsContext";
import { NoteContentElipsis } from "../Recipe-scroll/RecipeScroll";

import Skeleton from "react-loading-skeleton";

export default function SavedNav(Props) {
  useEffect(()=> {
    window.scrollTo({
      top: "0px",
    })
  })

  // Loading page
  const [load, setLoad] = useState(false);
  useEffect(() => {
      setTimeout(() => {
        setLoad(true);
      }, 200);
  }, []);

  // Darkmode Toggle
  const {modeToggle, setModeToggle} = useContext(DarkModeContext)

  // Save context
  // const {isSolid, setIsSolid} = useContext(SaveContext)
  const {IsProps1, setIsProps1} = useContext(SaveContext)
  const {setIsPopupProps1} = useContext(SaveContext)
  const [IsProps1Local, setIsProps1Local] = useState(false)

  const {IsProps2, setIsProps2} = useContext(SaveContext)
  const {setIsPopupProps2} = useContext(SaveContext)
  const [IsProps2Local, setIsProps2Local] = useState(false)

  const {IsProps3, setIsProps3} = useContext(SaveContext)
  const {setIsPopupProps3} = useContext(SaveContext)
  const [IsProps3Local, setIsProps3Local] = useState(false)

  const {IsProps4, setIsProps4} = useContext(SaveContext)
  const {IsPopupProps4, setIsPopupProps4} = useContext(SaveContext)
  const [IsProps4Local, setIsProps4Local] = useState(false)


  // Save count menu
  const {IsMenuCount, setIsMenuCount} = useContext(SaveContext)

  // Props1 
  function HandleSaveProps1() {
    setIsProps1((prev)=> !prev)
    setIsPopupProps1((prev)=> !prev)
  }
  useEffect(()=> {
    if(IsProps1) {
      setIsProps1Local(true)
    } else {
      setIsProps1Local(false)
    }

  }, [IsProps1, setIsProps1Local])
  // !----------------------------------

  // Props2 
  function HandleSaveProps2() {
    setIsProps2((prev)=> !prev)
    setIsPopupProps2((prev)=> !prev)
  }
  useEffect(()=> {
    if(IsProps2) {
      setIsProps2Local(true)
    } else {
      setIsProps2Local(false)
      
    }
  }, [IsProps2, setIsProps2Local])
  // !----------------------------------

  // Props3 
  function HandleSaveProps3() {
    setIsProps3((prev)=> !prev)
    setIsPopupProps3((prev)=> !prev)
  }
  useEffect(()=> {
    if(IsProps3) {
      setIsProps3Local(true)
    } else {
      setIsProps3Local(false)
    }
  }, [IsProps3, setIsProps3Local])
  // !----------------------------------

  // Props4 
  function HandleSaveProps4() {
    setIsProps4((prev)=> !prev)
    setIsPopupProps4((prev)=> !prev)
  }
  useEffect(()=> {
    if(IsProps4) {
      setIsProps4Local(true)
    } else {
      setIsProps4Local(false)
    }
  }, [IsProps4, setIsProps4Local])
  // !----------------------------------

  // Header style
  const HeaderSavedStyle = {
    display: "flex", justifyContent: "space-between", alignItems: "center",
    width: "100%", height: "fit-content",  color: modeToggle? "white" : "black", padding: "16px 16px 8px 16px", 
    backgroundColor: modeToggle ? "#000000" : "white" , 
    borderBottom: modeToggle ? '1px solid #292929' : '0px',
  }
  useEffect(()=> {
    if(IsProps1){
      setIsMenuCount(false)
    } else if(IsProps2){
      setIsMenuCount(false)
    } else if (IsProps3) {
      setIsMenuCount(false)
    } else if (IsProps4){
      setIsMenuCount(false)
    } else {
      setIsMenuCount(true)
    }
  }, [setIsMenuCount, IsProps1, IsProps2, IsProps3, IsProps4])

  // Blur input in explore
  const {setStateInputRef} = useContext(InputContext)
  useEffect(()=> {
    setStateInputRef(false)
  }, [setStateInputRef])

  const Navigate = useNavigate()

  // Save from ParamsMenuScroll
  const {StatusMenu, setStatusMenu} = useContext(ParamsSavedMenuContext)

  const {selectedDataMenuScr} = useContext(ParamContext);
  const {LoadingSkele} = useContext(ParamContext)
  const {MenuScroll} = useContext(ParamContext)

  const [DataMenuScrSave, setDataMenuScrSave] = useState([])
  useEffect(()=> {
    // setDataMenuScrSave(JSON.stringify(selectedDataMenuScr))
  },[DataMenuScrSave, selectedDataMenuScr])

  console.log(DataMenuScrSave + "DATA")

  



  // From ParamsMenuScroll
  useEffect(()=> {
    if(StatusMenu) {
      setIsMenuCount(false)
    }
  }, [StatusMenu, setIsMenuCount])

  return (
    <>
    
    
    <div className="SavedNav" style={{backgroundColor: modeToggle ? "black" : "white"}}>
      <div style={HeaderSavedStyle}>
        <p style={{fontSize: "14px", fontFamily: "Main-Bold"}}>Koleksi</p>
        <div style={{cursor: "pointer"}} onClick={()=> Navigate('/Explore')}>
          <i style={{fontSize: "24px"}} class="bi bi-plus"></i>
        </div>
        {/* <p style={{fontSize: "10px", fontFamily: "inter", color: modeToggle ? "var(--Inner-Input)" : "var(--SubHeading-shade)"}}>Menu tersimpan akan muncul disini</p> */}
      </div>

      <div style={{padding: modeToggle ? "16px" : "0px 16px"}}>
        {load ? (
          <>
          {IsMenuCount && (
              <div style={{width: "100%", height: "80vh", color: modeToggle ? "white" : "black", display: "flex", alignItems: "center", justifyContent: "center", flexDirection: "column"}}>
              <p style={{fontFamily: "inter", fontSize: "12px", color: modeToggle ? "var(--Inner-Input)" : "var(--SubHeading-shade)"}}>Tidak ada koleksi</p>
              </div>
          )}

                  <div style={{display: "flex", flexDirection: "column", gap: "8px", flexWrap: "wrap"}}>
                  <div style={{display: "flex", flexDirection: "row", gap: "8px", marginTop: "4px", flexWrap: "wrap"}}>

                    {/* Menu Props 1 */}
                    {IsProps1 && (
                      <>
                        {IsProps1Local && (
                          <div style={{display: "flex"}}>
                            <ContentNewSect
                            DarkMode={modeToggle}
                            // Toggle Save
                            SavedProps1={(prev)=> !prev}
                            SaveBoxProps1={<div className="Save-coock-props" onClick={HandleSaveProps1} style={{backgroundColor: Props.DarkMode ? "var(--Shade-color)" : "var(--Shade-color)"}}>
                            {IsProps1 ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>} </div>}
                            Src="/Image/Food-img/image 2img1.jpg"
                            Alt="Image of Recipe"
                            Title="Sup udang"
                            Bahan="5 Bahan"
                            Desc="Sup udang enak pokoknya coba aja dulu, pasti enak dan simpel gacor kang"
                            waktuMasak="36 Menit"
                            />
                          </div>
                        )}
                      </>
                    )}
                    
                    {/* Menu Props 2 */}
                    {IsProps2 && (
                      <>
                        {IsProps2Local && (
                          <ContentNewSect
                          DarkMode={modeToggle}
                          SavedProps2={(prev)=> !prev}
                          SaveBoxProps2={<div className="Save-coock-props" onClick={HandleSaveProps2} style={{backgroundColor: Props.DarkMode ? "var(--Shade-color)" : "var(--Shade-color)"}}>
                          {IsProps2 ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>} </div>}
                          Src="/Image/Food-img/Image 3.jpg"
                          Alt="Image of Recipe"
                          Title="Serundeng"
                          Bahan="7 Bahan"
                          Desc="Sup udang enak pokoknya coba aja dulu, pasti enak dan simpel gacor kang"
                          waktuMasak="24 Menit"
                          />
                        )}
                      </>
                    )}

                    {/* Menu Props 3 */}
                    {IsProps3 && (
                      <>
                        {IsProps3Local && (
                          <ContentNewSect
                          DarkMode={modeToggle}
                          SavedProps3={(prev)=> !prev}
                          SaveBoxProps3={<div className="Save-coock-props" onClick={HandleSaveProps3} style={{backgroundColor: Props.DarkMode ? "var(--Shade-color)" : "var(--Shade-color)"}}>
                          {IsProps3 ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>} </div>}
                          Src="/Image/Banner.png"
                          Alt="Image of Recipe"
                          Title="Acikiwiw"
                          Bahan="9 Bahan"
                          Desc="Sup udang enak pokoknya coba aja dulu, pasti enak dan simpel gacor kang"
                          />
                        )}
                      </>
                    )}

                    {/* Menu Props 4 */}
                    {IsProps4 && (
                      <>
                        {IsProps4Local && (
                          <ContentNewSect
                          DarkMode={modeToggle}
                          SavedProps4={(prev)=> !prev}
                          SaveBoxProps4={<div className="Save-coock-props" onClick={HandleSaveProps4} style={{backgroundColor: Props.DarkMode ? "var(--Shade-color)" : "var(--Shade-color)"}}>
                          {IsProps4 ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>} </div>}
                          Src="/Image/Banner.png"
                          Alt="Image of Recipe"
                          Title="Slemehoy"
                          Bahan="12 Bahan"
                          Desc="Sup udang enak pokoknya coba aja dulu, pasti enak dan simpel gacor kang"
                          />
                        )}
                      </>
                    )}

                        {MenuScroll.length > 0 && (
                          <>
                            {MenuScroll[4].SaveMenu && (
                              <div onClick={()=> Navigate(`/Explore/Menu/${selectedDataMenuScr.NameRecp}-${selectedDataMenuScr.id}`)}>
                                  <RecipeScrollContent DarkMode={modeToggle} RemovePaddingInSavedSect={{padding: "0px"}}
                                  Title={<div>{LoadingSkele ? <Skeleton height={12} width={'110%'}  /> : (MenuScroll.length > 0 ? <p>{MenuScroll[4].NameRecp}</p> : <p>Loading..</p>)}</div>} 
                                  
                                  Desc={<div>{LoadingSkele ? <Skeleton height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p style={NoteContentElipsis}>{MenuScroll[4].desc}</p> : <p>Loading..</p>)}</div>} 
                                  
                                  Bahan={<div>{LoadingSkele ? <Skeleton height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p>{MenuScroll[4].BahanRecp.length + " Bahan"}</p> : <p>Loading..</p>)}</div>}
                  
                                  Img={LoadingSkele ? <Skeleton style={{ borderRadius: "8px"}} height={"100%"} width={'100%'} count={1} /> : (MenuScroll.length > 0 ? (<img src={MenuScroll[4].img} alt={MenuScroll[4].NameRecp} width={"100%"} height={"100%"} style={{borderRadius: "8px", objectFit: "cover",}}/>) : '')}
                                  />
                                </div>
                            )}
                            </>
                        )}

                        {MenuScroll.length > 0 && (
                          <>
                            {MenuScroll[2].SaveMenu && (
                              <div onClick={()=> Navigate(`/Explore/Menu/${selectedDataMenuScr.NameRecp}-${selectedDataMenuScr.id}`)}>
                              <RecipeScrollContent DarkMode={modeToggle} RemovePaddingInSavedSect={{padding: "0px"}}
                              Title={<div>{LoadingSkele ? <Skeleton height={12} width={'110%'}  /> : (MenuScroll.length > 0 ? <p>{MenuScroll[2].NameRecp}</p> : <p>Loading..</p>)}</div>} 
                              
                              Desc={<div>{LoadingSkele ? <Skeleton height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p style={NoteContentElipsis}>{MenuScroll[2].desc}</p> : <p>Loading..</p>)}</div>} 
                              
                              Bahan={<div>{LoadingSkele ? <Skeleton height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p>{MenuScroll[2].BahanRecp.length + " Bahan"}</p> : <p>Loading..</p>)}</div>}
              
                              Img={LoadingSkele ? <Skeleton style={{ borderRadius: "8px"}} height={"100%"} width={'100%'} count={1} /> : (MenuScroll.length > 0 ? (<img src={MenuScroll[2].img} alt={MenuScroll[2].NameRecp} width={"100%"} height={"100%"} style={{borderRadius: "8px", objectFit: "cover",}}/>) : '')}
                              />
                            </div>
                            )}
                          </>
                        )}
                      
                  </div>
              </div>
          </>
        ) : (
          <LoadingPage DarkMode={modeToggle} SavedHeight={{height: "100vh"}}/>
        )}

      </div>
    </div>

      {modeToggle ? (
            <BottomNav DarkMode={modeToggle} />
          ) : (
            <BottomNav />
      )}
    </>
  );
}
