import { useEffect, useState} from "react";
import { useNavigate } from "react-router-dom";
import RecipeScrollContent from "./RecipeScrollContent";
import AOS from "aos";
import { Link } from "react-router-dom";

// Darkmode
import { DarkModeContext } from "../../DarkMode/Darkmode";
import { useContext } from "react";

import { ParamContext } from "../../NewPageCozDataMap/paramsContext";
import Skeleton from 'react-loading-skeleton';

import { ParamsSavedMenuContext } from "../../NewPageCozDataMap/PageDataMenuScroll/ContextSaveMenu";

export const NoteContentElipsis = {
  display: '-webkit-box',
  WebkitLineClamp: 2, // Number of lines to show
  WebkitBoxOrient: 'vertical',
  overflow: 'hidden',
  textOverflow: 'ellipsis',
}

export default function RecipeScroll(Props) {
    useEffect(()=> {
        AOS.init({duration: 1000})
    })

    // Darkmode
    const {modeToggle, setModeToggle} = useContext(DarkModeContext)

    const {MenuScroll, setMenuScroll} = useContext(ParamContext)
    const {LoadingSkele, setLoadingSkele} = useContext(ParamContext)

    const SkeletonDarkmode = {
      backgroundImage: "linear-gradient(90deg, var(--base-color), var(--highlight-color), var(--base-color))"
    }


    const {setSelectedDataMenuScr, SelectedDataMenuScroll} = useContext(ParamContext)
    const Navigate = useNavigate()
    function DirectMenu(MenuItem) {
      Navigate(`/Explore/Menu/${MenuItem.NameRecp}-${MenuItem.id}`)
      setSelectedDataMenuScr(MenuItem)
    }

  return (
    <>
      <style>
        {`
            .RecipeScroll {
                min-width: 328px;
                height: 270px;
            }

            .Wrapper-recipeScroll {
                width: 100%;
                height: 100%;

                display: "flex";
                flex-direction: "column";
                gap: "8px";
            }

            `}
      </style>
      <div className="RecipeScroll" 
    //   data-aos="fade-up" 
      data-aos-delay="1100"
      style={{height: "120vh"}}>
        <div className="Wrapper-recipeScroll">
          
            {/* CARD 1 */}
            <div onClick={()=> {DirectMenu(MenuScroll[4]); SelectedDataMenuScroll(MenuScroll[4])}}>
              {modeToggle ? (
                <RecipeScrollContent DarkMode={modeToggle} 
                Title={<div>{LoadingSkele ? <Skeleton style={{backgroundImage: modeToggle ? SkeletonDarkmode : ""}} height={12} width={'110%'}  /> : (MenuScroll.length > 0 ? <p>{MenuScroll[4].NameRecp}</p> : <p>Loading..</p>)}</div>} 
                
                Desc={<div>{LoadingSkele ? <Skeleton height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p style={NoteContentElipsis}>{MenuScroll[4].desc}</p> : <p>Loading..</p>)}</div>} 
                
                Bahan={<div>{LoadingSkele ? <Skeleton height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p>{MenuScroll[4].BahanRecp.length + " Bahan"}</p> : <p>Loading..</p>)}</div>}

                Img={LoadingSkele ? <Skeleton style={{ borderRadius: "8px"}} height={"100%"} width={'100%'} count={1} /> : (MenuScroll.length > 0 ? (<img src={MenuScroll[4].img} alt={MenuScroll[4].NameRecp} width={"100%"} height={"100%"} style={{borderRadius: "8px", objectFit: "cover",}}/>) : '')}

                />
              ) : (
                <RecipeScrollContent 
                Title={<div>{LoadingSkele ? <Skeleton height={12} width={'110%'}  /> : (MenuScroll.length > 0 ? <p>{MenuScroll[4].NameRecp}</p> : <p>Loading..</p>)}</div>}
                
                Desc={<div>{LoadingSkele ? <Skeleton style={{ borderRadius: "2px"}} height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p style={NoteContentElipsis}>{MenuScroll[4].desc}</p> : <p>Loading..</p>)}</div>}
                
                Bahan={<div>{LoadingSkele ? <Skeleton height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p>{MenuScroll[4].BahanRecp.length + " Bahan"}</p> : <p>Loading..</p>)}</div>}

                Img={LoadingSkele ? <Skeleton style={{ borderRadius: "8px"}} height={"100%"} width={'100%'} count={1} /> : (MenuScroll.length > 0 ? (<img src={MenuScroll[4].img} alt={MenuScroll[4].NameRecp} width={"100%"} height={"100%"} style={{borderRadius: "8px", objectFit: "cover"}}/>) : '')}

                />
              )}
            </div>

            {/* CARD 2 */}
            <div onClick={()=> {DirectMenu(MenuScroll[2]); SelectedDataMenuScroll(MenuScroll[2])}}>
              {modeToggle ? (
               <RecipeScrollContent DarkMode={modeToggle}
               Title={<div>{LoadingSkele ? <Skeleton height={12} width={'110%'}  /> : (MenuScroll.length > 0 ? <p>{MenuScroll[2].NameRecp}</p> : <p>Loading..</p>)}</div>} 
               
               Desc={<div>{LoadingSkele ? <Skeleton style={{ borderRadius: "2px"}} height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p style={NoteContentElipsis}>{MenuScroll[2].desc}</p> : <p>Loading..</p>)}</div>} 

               Bahan={<div>{LoadingSkele ? <Skeleton height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p>{MenuScroll[2].BahanRecp.length + " Bahan"}</p> : <p>Loading..</p>)}</div>}

               Img={LoadingSkele ? <Skeleton style={{ borderRadius: "8px"}} height={"100%"} width={'100%'} count={1} /> : (MenuScroll.length > 0 ? (<img src={MenuScroll[2].img} alt={MenuScroll[2].NameRecp} width={"100%"} height={"100%"} style={{borderRadius: "8px", objectFit: "cover",}}/>) : '')}

               />
              ) : (
                <RecipeScrollContent Title={<div>{LoadingSkele ? <Skeleton height={12} width={'110%'}  /> : (MenuScroll.length > 0 ? <p>{MenuScroll[2].NameRecp}</p> : <p>Loading..</p>)}</div>} 
                
                Desc={<div>{LoadingSkele ? <Skeleton style={{ borderRadius: "2px"}} height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p style={NoteContentElipsis}>{MenuScroll[2].desc}</p> : <p>Loading..</p>)}</div>} 

                Bahan={<div>{LoadingSkele ? <Skeleton height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p>{MenuScroll[2].BahanRecp.length + " Bahan"}</p> : <p>Loading..</p>)}</div>}

                Img={LoadingSkele ? <Skeleton style={{ borderRadius: "8px"}} height={"100%"} width={'100%'} count={1} /> : (MenuScroll.length > 0 ? (<img src={MenuScroll[2].img} alt={MenuScroll[2].NameRecp} width={"100%"} height={"100%"} style={{borderRadius: "8px", objectFit: "cover",}}/>) : '')}
                
                />
              )}
            </div>

            {/* CARD 3 */}
            <div onClick={()=> {DirectMenu(MenuScroll[3]); SelectedDataMenuScroll(MenuScroll[3])}}>
              {modeToggle ? (
                <RecipeScrollContent DarkMode={modeToggle} 
                Title={<div>{LoadingSkele ? <Skeleton style={{backgroundImage: modeToggle ? SkeletonDarkmode : ""}} height={12} width={'110%'}  /> : (MenuScroll.length > 0 ? <p>{MenuScroll[3].NameRecp}</p> : <p>Loading..</p>)}</div>} 
                
                Desc={<div>{LoadingSkele ? <Skeleton height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p style={NoteContentElipsis}>{MenuScroll[3].desc}</p> : <p>Loading..</p>)}</div>} 
                
                Bahan={<div>{LoadingSkele ? <Skeleton height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p style={NoteContentElipsis}>{MenuScroll[3].BahanRecp.length+ " Bahan"}</p> : <p>Loading..</p>)}</div>}
                
                Img={LoadingSkele ? <Skeleton style={{ borderRadius: "8px"}} height={"100%"} width={'100%'} count={1} /> : (MenuScroll.length > 0 ? (<img src={MenuScroll[3].img} alt={MenuScroll[3].NameRecp} width={"100%"} height={"100%"} style={{borderRadius: "8px"}}/>) : '')}

                />
              ) : (
                <RecipeScrollContent 
                Title={<div>{LoadingSkele ? <Skeleton height={12} width={'110%'}  /> : (MenuScroll.length > 0 ? <p>{MenuScroll[3].NameRecp}</p> : <p>Loading..</p>)}</div>}
                
                Desc={<div>{LoadingSkele ? <Skeleton style={{ borderRadius: "2px"}} height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p style={NoteContentElipsis}>{MenuScroll[3].desc}</p> : <p>Loading..</p>)}</div>}

                Bahan={<div>{LoadingSkele ? <Skeleton height={8} width={'80%'} count={2} /> : (MenuScroll.length > 0 ? <p style={NoteContentElipsis}>{MenuScroll[3].BahanRecp.length + " Bahan"}</p> : <p>Loading..</p>)}</div>}
                 
                TimeCook="28 Menit"
                
                Img={LoadingSkele ? <Skeleton style={{ borderRadius: "8px"}} height={"100%"} width={'100%'} count={1} /> : (MenuScroll.length > 0 ? (<img src={MenuScroll[3].img} alt={MenuScroll[3].NameRecp} width={"100%"} height={"100%"} style={{borderRadius: "8px"}}/>) : '')}

                />
              )}
            </div>


            
        </div>
      </div>

    </>
  );
}
