import { createContext, useState, useEffect} from "react";

export const SaveContext = createContext()

export default function SaveMenuProvider({children}) {
    const [isSolid, setIsSolid] = useState(false)

    // Menu Count 
  const [IsMenuCount, setIsMenuCount] = useState(true)
    // Props 1
    const [IsProps1, setIsProps1] = useState(()=> {
        const savedMenuProps1 = localStorage.getItem('SavedProps1');
        return savedMenuProps1 === 'true';
    })
    // Popup props 1
    const [IsPopupProps1, setIsPopupProps1] = useState(()=> {
        const savedMenuProps1 = localStorage.getItem('SavedProps1');
        return savedMenuProps1 === 'true';
    })
    useEffect(() => {
        localStorage.setItem('SavedProps1', IsProps1);
    }, [IsProps1, IsPopupProps1]);
    // !----------------------------------

    // Props 2
    const [IsProps2, setIsProps2] = useState(()=> {
        const savedMenuProps2 = localStorage.getItem('SavedProps2');
        return savedMenuProps2 === 'true';
    })
    // Popup props 2
    const [IsPopupProps2, setIsPopupProps2] = useState(()=> {
        const savedMenuProps2 = localStorage.getItem('SavedProps2');
        return savedMenuProps2 === 'true';
    })
    useEffect(() => {
        localStorage.setItem('SavedProps2', IsProps2);
    }, [IsProps2, IsPopupProps1]);
    // !----------------------------------

    // Props 3
    const [IsProps3, setIsProps3] = useState(()=> {
        const savedMenuProps3 = localStorage.getItem('SavedProps3');
        return savedMenuProps3 === 'true';
    })
    const [IsPopupProps3, setIsPopupProps3] = useState(()=> {
        const savedMenuProps3 = localStorage.getItem('SavedProps3');
        return savedMenuProps3 === 'true';
    })
    useEffect(() => {
        localStorage.setItem('SavedProps3', IsProps3);
    }, [IsProps3, IsPopupProps3]);
    // !----------------------------------

    // Props 4
    const [IsProps4, setIsProps4] = useState(()=> {
        const savedMenuProps4 = localStorage.getItem('SavedProps4');
        return savedMenuProps4 === 'true';
    })
    const [IsPopupProps4, setIsPopupProps4] = useState(()=> {
        const savedMenuProps4 = localStorage.getItem('SavedProps4');
        return savedMenuProps4 === 'true';
    })
    useEffect(() => {
        localStorage.setItem('SavedProps4', IsProps4);
    }, [IsProps4, IsPopupProps4]);
    // !----------------------------------

    const ValueToSend = {
        isSolid, setIsSolid, 
        IsMenuCount, setIsMenuCount,

        IsProps1, setIsProps1, 
        IsPopupProps1, setIsPopupProps1,

        IsProps2, setIsProps2, 
        IsPopupProps2, setIsPopupProps2,

        IsProps3, setIsProps3, 
        IsPopupProps3, setIsPopupProps3,

        IsProps4, setIsProps4,
        IsPopupProps4, setIsPopupProps4,
    }

    return (
        <SaveContext.Provider value={ValueToSend}>
            {children}
        </SaveContext.Provider>
    )
}