import { useEffect, useRef, useState } from "react";
import BottomNav from "../../Nav-comp/Bottom-nav/Bottom-nav";
import Header from "../../Nav-comp/Top-nav/Header";
import ImageSlider from "../../Board-Home/Comp-img-slider/Img-slider";
import LoadingPage from "../../Loading-page/Loadcomp";
import { InputContext } from "../../Nav-comp/Top-nav/SendRef";
import { useContext } from "react";
import { useNavigate, useLocation, Navigate } from "react-router-dom";

import '../../Newest-recipe-sect/New-recipe-section.css';


// Darkmode Context
import { DarkModeContext } from "../../../DarkMode/Darkmode";
import RecipeScroll from "../../Recipe-scroll/RecipeScroll";

import {v4 as uuidv4} from 'uuid'

import ParamProvider from "../../../NewPageCozDataMap/paramsContext";
import { ParamContext } from "../../../NewPageCozDataMap/paramsContext";

export default function ExploreComp(Props) {
  useEffect(() => {
    window.scrollTo({
      top: "0px",
    });
  }, []);

  const [loadPage, setLoadPage] = useState(false)
  useEffect(()=> {
    setTimeout(()=> {
      setLoadPage(true)
    }, 300)
  })

  // Darkmode Toggle
  const {modeToggle} = useContext(DarkModeContext)

  // Input Context
  const { StateInputRef} = useContext(InputContext)
  
  const {InputRefFocus, setRemoveInputHeader} = useContext(InputContext)

  const {getData, setData} = useContext(ParamContext)
  const {ShowOutput, SetOutput} =  useContext(ParamContext)
  const {selectedData, setSelectedData, SelectedDataItem} = useContext(ParamContext)


  // X-mark input = Handle clear value
  const HandleClearInput = () => {
    InputRefFocus.current.value = null;
    SetOutput(false)
  }

  // Active input
  useEffect(()=> {
    if(StateInputRef) {
      InputRefFocus.current.focus()
      setRemoveInputHeader(false)
    } 
  }, [StateInputRef,  setRemoveInputHeader, InputRefFocus])

  // Disable scroll if output from input is showing
  useEffect(()=> {
    document.body.style.overflow= ShowOutput ? "hidden" : "auto"
  }, [ShowOutput])

  const {DataMapInput, setDataMapInput} = useContext(ParamContext)


  const HandleChange = (e) => {
    const Filtered = e.target.value;
    const ReadyFilter = DataMapInput.filter(
      (item) =>
        item.NameRecp.toLowerCase().includes(Filtered)
    );

    const InputRef = InputRefFocus.current
    if(InputRef) {
      InputRef.addEventListener('keydown', (e)=> {
        if(e.key === "Enter") {
          if(getData.length > 0) {
            getData.map(item => 
              InputRef.value = item.NameRecp
            )
          }
        }
      })
    }

    // ReadyFilter.sort((a, b) => {
    //   const nameA = a.first_name.toLowerCase();
    //   const nameB = b.first_name.toLowerCase();
    //   if (nameA < nameB) return -1; 
    //   if (nameA > nameB) return 1; 
    //   return 0; // 0 = Jika Alfabet sama
    // });
    if (Filtered) {
      SetOutput(true);
    } else {
      SetOutput(false);
    }

    // const DelayOutput = setTimeout(()=> {
      setData(ReadyFilter);
    // }, 1000)
    
    
    // return ()=>{
      // clearTimeout(DelayOutput)
    // }

   

  };

  const Navigate = useNavigate()


  function MapClickedData(No, Namadepan) {
    // const uuid = uuidv4()
    Navigate(`/Explore/Search/${Namadepan}-${No}`)
    if(InputRefFocus.current) {
      InputRefFocus.current.blur()
    }
    SetOutput(false)
  }

  const [ShowSuggest, setShowSuggest] = useState(false)
  const ListSearchSuggest = ['Roti isi', 'List 1', 'List 2', 'List 3', 'List 4', 'List 5', 'List 6']
  // Show suggest if input acticve/focus
  useEffect(()=> {
    const InputFocus = InputRefFocus.current
    if(InputFocus) {
      InputFocus.addEventListener('focus', ()=> {
        setShowSuggest(true)
      })
    } if (InputFocus) {
      InputFocus.addEventListener('input', ()=> {
        setShowSuggest(false)
      })
    } 
    if (InputFocus) {
        InputFocus.addEventListener('blur', ()=> {
          setShowSuggest(false)
        })
      }
  }, [setShowSuggest, InputRefFocus])

  function HandleSuggestClick(item) {
    const InputRef = InputRefFocus.current
    if (InputRef) {
        InputRef.focus()
        InputRef.value = item
        if(InputRef.value === item) {
          SetOutput(true)
        }
      }
  }

  return (
    <>
      {modeToggle ? (
        <div className="Explore-comp" style={{backgroundColor: "black"}}>
          <header>
            <div className="nav-top" style={{backgroundColor: "black"}}>
              <p>Explore</p>
              <div >
                <Header 
                  DarkMode={modeToggle}
                  NewWidth={{width: '100%'}}
                  NewHeight={{ height: "fit-content" }}
                  HiddenSetting={{ display: "none" }}
                  NewSearchBox={{ width: "100%" }}
                  HiddenBorderBottom={{BorderBottom: "0px"}}
                  // OutputInput={OutputInput}
                  Input={ <input
                    type="search"
                    placeholder="Ingin masak apa hari ini?"
                    onChange={HandleChange}
                    
                    // ref={ValueInputRef}
                    ref={InputRefFocus}
                  />}
                  ClearInput={<div className="X-mark" style={{display: ShowOutput ? "flex" : "none" }} onClick={HandleClearInput}>
                  <i class="fa-solid fa-xmark"></i>
                </div>}
                />
                
                {ShowSuggest && (
                <div style={{padding: "12px 16px 16px 16px", display: "flex", flexDirection: "column", gap: "12px"}}>
                  {/* <p style={{fontSize: "14px"}}>Orang orang juga menelusuri: </p> */}
                  <ul style={{listStyle: "none", display: "flex", gap: "8px", flexWrap: "wrap"}}>
                  {ListSearchSuggest.map((item, index) => (
                      <li key={index} onClick={()=> HandleSuggestClick(item)} style={{width: "fit-content", padding: "4px 8px", fontSize: "12px", borderRadius: "4px", backgroundColor: "rgb(41, 41, 41)", color: "#999999", fontWeight: "600",cursor: "pointer"}}>{item}</li>
                    ))}
                  </ul>
                </div>
              )}
              </div>
            </div>
          </header>
          {/* Output Input */}
          <div
            className="Output-Input"
            style={{ height: ShowOutput ? "100%" : "0px", zIndex: "45", backgroundColor: modeToggle ? "black" : '',}}
          >
            <div style={{paddingTop: modeToggle ? "16px" : ''}}>
            <ParamProvider>
              {ShowOutput && (
                <>
                {getData.length > 0 ? (
                  getData.map((item, index) => (
                    <div
                      className="Wrapper-output-input"
                      style={{ display: ShowOutput ? "flex" : "none" }}
                    >
                      <p style={{height: "40px", width: "100%", backgroundColor: "rgb(41, 41, 41)", color: "white", borderRadius: "8px", display: 'flex', alignItems: "center", padding: "12px", justifyContent: "space-between" , cursor: "pointer"}} onClick={()=> {SelectedDataItem(item); MapClickedData(index, item.NameRecp);}}>
                        {/* {index + 1 + `. `} */}
                        {item.NameRecp}
                      <img src="/Icons/Right_Arrow_Darkmode.svg" alt="RightArrow"></img>
                      </p>
                    </div>
                  ))
                ) : (
                  <p style={{ padding: "16px 24px", opacity: "50%", fontSize: "12px" }}>
                    Resep tidak ditemukan.
                  </p>
                )}
                </>
              )}
              </ParamProvider>
            </div>

            {(getData.value = "" ? <p>Tidak ada data</p> : <div></div>)}
          </div>
          
          {/* Content to load */}
              {/* <div className="Image-slider"> */}
                {/* <ImageSlider NewMargin={{margin: "0px 0px"}} HideIndicator={{indicators: false}} /> */}
              {/* </div> */}
              <RecipeScroll DisableViewMore={{display: "none"}} PropsHeight={{height: "fit-content"}}/>
          
            {modeToggle ? (
              <BottomNav DarkMode={modeToggle} />
            ) : (
              <BottomNav />
            )}
        </div>
      ) : (
        <div className="Explore-comp" style={{backgroundColor: "var(--Ground-White)"}}>
          <header>
            <div className="nav-top">
              <p>Explore</p>
              <div>
                <Header 
                    DarkMode={modeToggle}
                    NewWidth={{width: '100%'}}
                    NewHeight={{ height: "fit-content" }}
                    HiddenSetting={{ display: "none" }}
                    NewSearchBox={{ width: "100%" }}
                    // OutputInput={OutputInput}
                    Input={ <input
                      inputMode="search"
                      type="Search"
                      placeholder="Ingin masak apa hari ini?"
                      onChange={HandleChange}
                      // ref={ValueInputRef}
                      ref={InputRefFocus}
                    />}
                    ClearInput={<div className="X-mark" style={{display: ShowOutput ? "flex" : "none" }} onClick={HandleClearInput}>
                    <i class="fa-solid fa-xmark"></i>
                  </div>}
                  />
                  {ShowSuggest && (
                <div style={{padding: "0px 16px 16px 16px", display: "flex", flexDirection: "column", gap: "12px"}}>
                  {/* <p style={{fontSize: "14px"}}>Orang orang juga menelusuri: </p> */}
                  <ul style={{listStyle: "none", display: "flex", gap: "8px", flexWrap: "wrap"}}>
                  {ListSearchSuggest.map((item, index) => (
                      <li key={index} onClick={()=> HandleSuggestClick(item)} style={{width: "fit-content", padding: "4px 8px", fontSize: "12px", borderRadius: "4px", backgroundColor: "rgb(41, 41, 41)", color: "#999999", fontWeight: "600", cursor: "pointer"}}>{item}</li>
                    ))}
                  </ul>
                </div>
              )}
              </div>
            </div>
          </header>
          {/* Output Input */}
          <div
            className="Output-Input"
            style={{ height: ShowOutput ? "100%" : "0px", zIndex: "45"}}
          >
            <div>
              <ParamProvider>
              {ShowOutput && (
                <>
                {getData.length > 0 ? (
                  getData.map((item, index) => (
                    <div
                      className="Wrapper-output-input"
                      style={{ display: ShowOutput ? "flex" : "none" }}
                    >
                      <p style={{height: "40px", width: "100%", backgroundColor: "rgb(41, 41, 41)", color: "white", borderRadius: "8px", display: 'flex', alignItems: "center", padding: "12px", justifyContent: "space-between" , cursor: "pointer"}} onClick={()=> {SelectedDataItem(item); MapClickedData(index, item.NameRecp);}}>
                        {/* {index + 1 + `. `} */}
                        {item.NameRecp}
                      <img src="/Icons/Right_Arrow_Darkmode.svg" alt="RightArrow"></img>
                      </p>
                    </div>
                  ))
                ) : (
                  <p style={{ padding: "16px 24px", opacity: "50%", fontSize: "12px" }}>
                    Resep tidak ditemukan.
                  </p>
                )}
                </>
              )}
              </ParamProvider>
            </div>

            {(getData.value = "" ? <p>Tidak ada data</p> : <div></div>)}
          </div>

          {/* Content to load */}
              {/* <div className="Image-slider"> */}
                {/* <ImageSlider NewMargin={{margin: "0px 0px"}} HideIndicator={{indicators: false}} /> */}
              {/* </div> */}

              

              {/* <div className="Top-new-sect">
              <div style={{marginTop: "12px"}}>
                <h1 style={{color: Props.DarkMode ? "white" : "black"}}>List resep</h1>
                <p style={{color: Props.DarkMode ? "var(--Inner-Input)" : "var(--SubHeading-shade)"}}>Pilih yang kamu suka</p>
              </div>
            </div> */}

              

              
              <RecipeScroll DisableViewMore={{display: "none"}} PropsHeight={{height: "fit-content"}}/>
          

            {modeToggle ? (
              <BottomNav DarkMode={modeToggle} />
            ) : (
              <BottomNav />
            )}
        </div>
      )}
    
    
    </>
    
  );
}