import { useContext, useState } from "react";
import { ParamContext } from "../paramsContext";
import BottomNav from "../../Component/Nav-comp/Bottom-nav/Bottom-nav";
import { useEffect } from "react";
import { DarkModeContext } from "../../DarkMode/Darkmode";
import { useNavigate } from "react-router-dom";

// React player
import VideoPlayer from "./VideoPlayer";

// Params context menu save
import { ParamsSavedMenuContext } from "./ContextSaveMenu";

export default function ParamsMenuScroll({SaveBtn}) {
	// Darkmode
	const {modeToggle} = useContext(DarkModeContext)
  const {selectedDataMenuScr, setSelectedDataMenuScr} = useContext(ParamContext);

	const ListStyle = {
		fontFamily: "inter",
		fontSize: "12px",
		whiteSpace: "pre-wrap",
		color: modeToggle ? "#999999" : "var(--SubHeading-shade)"
	}

	const BahanDiv = {
		marginTop: "12px",
		width: "100%",
		height: "fit-content",
		display: "flex",
		flexDirection: "column",
		gap: "8px",
		padding: "16px",
		borderRadius: "16px",
		backgroundColor: modeToggle ? "rgb(41, 41, 41)" : "var(--Ground-new-gray)",
		fontFamily: "inter"
	}

	const ImgPrev = {
		width: "100%",
		height: "150px",
		borderRadius: "8px"
	}

	// List Function
	const [CheckList] = useState([selectedDataMenuScr.BahanRecp])
	const Bahan = ` Bahan untuk ${selectedDataMenuScr.NameRecp}`
	
	// Langkah-langkah
	const [HowToDo] = useState([selectedDataMenuScr.HowTo])
	const HowTo = `Langkah-langkah`

	// Source resep
	const [SourceRecipe] = useState([selectedDataMenuScr.SourceRecp])
	const [Author] = useState([selectedDataMenuScr.Author])
	const Source = `Recipe by ${Author}`

	const [LinkSource, setLinkSource] = useState(false)
	useEffect(()=> {
		const Author = selectedDataMenuScr.Author
		Author ? setLinkSource(true) : setLinkSource(false)
	}, [selectedDataMenuScr.Author])


	// Video src 
	const [ShowVid, setShowVid] = useState(false)
	useEffect(()=> {
		const SrcVid = selectedDataMenuScr.LinkVid
		SrcVid ? setShowVid(true) : setShowVid(false)
	}, [selectedDataMenuScr.LinkVid])

	// Navigate
	const Navigate = useNavigate()

	// Params Save Context
    const {StatusMenu, setStatusMenu, StatusSaveParams} = useContext(ParamsSavedMenuContext)
	

    // const [SaveMenu, setSaveMenu] = useState([selectedDataMenuScr.SaveMenu])

    
   console.log(StatusMenu + "STAUTS")

  console.log(selectedDataMenuScr.SaveMenu)



  return (
    <div style={{ width: "100%", height: "100vh" }}>
      {selectedDataMenuScr && (
        <div style={{ padding: "0px 16px 16px 16px", height: "fit-content", backgroundColor: modeToggle ? "black" : "white", paddingBottom: "64px"}}>

					{/* NAV (Back page) */}
					<div style={{display: "flex", flexDirection: "row", gap: "12px", color: modeToggle ? "white" : "black", padding: "16px 0px 16px 0px", alignItems: "center", width: "100%", justifyContent: "space-between", position: "sticky", top: "0px", backgroundColor: modeToggle ? "black" : "white"}}>
            <div onClick={()=> Navigate('/Explore')} style={{display: "flex", gap: "12px", alignItems: "center", cursor: "pointer"}} >
						  <i class="fa-solid fa-arrow-left"></i> 
              <p style={{fontSize: "14px", fontWeight: "600"}}>Back</p>
            </div>
			<div onClick={()=> StatusSaveParams(selectedDataMenuScr.SaveMenu)}>
                <div style={{color: modeToggle ? "white" : "black", fontSize: "14px", cursor: "pointer"}}>
                  {selectedDataMenuScr.SaveMenu ? <i class="fa-solid fa-bookmark"></i> : <i class="fa-regular fa-bookmark"></i>}
                </div>
            </div>

					</div>
          
					{/* Image */}
					<div style={{...ImgPrev, backgroundImage: `src(${selectedDataMenuScr.img})`}}>
						<img src={selectedDataMenuScr.img} alt="" width={"100%"} height={"100%"} style={{borderRadius: "8px", objectFit: "cover"}}/>
					</div>

					{/* Title */}
					<div style={{marginTop: "12px", fontFamily: "inter"}}>
          	<h3 style={{color: modeToggle ? "white" : "black", fontSize: "14px"}}>{selectedDataMenuScr.NameRecp}</h3>
          	<p style={{fontSize: "12px", color: modeToggle ? "#999999" : "var(--SubHeading-shade)", marginTop: "8px", marginBottom: "24px"}}>{selectedDataMenuScr.desc}</p>
					</div>

					{/* Bahan bahan */}
					<div style={BahanDiv}>
						<p style={{textAlign: "center", fontSize: "13px", fontWeight: "600", marginBottom: "12px", color: modeToggle ? "white" : "black"}}>{Bahan}</p>
						{CheckList.map((item, index) => 
							<p key={index} style={{...ListStyle, lineHeight: "20px"}}>{item}</p>
						)}
					</div>

					{/* Video Tutorial */}
					{ShowVid && (
						<div style={{marginTop: "12px", borderRadius: "12px"}}>
							<VideoPlayer SourceVid={selectedDataMenuScr.LinkVid} />
						</div>
					)}

					{/* Langkah langkah */}
					<div style={BahanDiv}>
						<p style={{textAlign: "center", fontSize: "13px", fontWeight: "600", marginBottom: "12px", color: modeToggle ? "white" : "black"}}>{HowTo}</p>
						{HowToDo.map((item, index) => 
							<p key={index} style={{...ListStyle, lineHeight: "20px", marginBottom: "12px"}}>{item}</p>
						)}
					</div>

					{/* Source */}
					<div style={{...ListStyle, marginTop: "32px", fontSize: "10px", textAlign: "center",}}>
						{LinkSource ? (
							<>
								<p style={{marginBottom: "4px"}}><i>{Source}</i></p>
								<a style={{color: modeToggle ? "#999999" : "var(--SubHeading-shade)", textDecoration: "underline",}} href={SourceRecipe} target="blank"><i>{SourceRecipe}</i></a>
							</>
						) : (
							<>
								<p><i>{Source}{SourceRecipe}</i></p>
							</>
						)}
					</div>
        </div>
      )}
      <BottomNav DarkMode={modeToggle} />
    </div>
  );
}
