// import ReactPlayer from "react-player"
export default function VideoPlayer({SourceVid}) {
    // const StyleVid = {
    //     borderRadius: "12px",
    //     height: "100%",
    //     width: "100%",
    // }
    return (
        <div>
           <video style={{borderRadius: "16px"}} width="100%" height="100%" controls controlsList="nodownload" autoPlay muted>
                <source src={SourceVid} type="video/mp4"/>
            </video>
        </div>
    )
}