import { useState, useEffect, useContext, createContext } from "react";
import "../../Component/Board-Home/Banner-cta.css";

import { FeedBackContext } from "./FeedbackContext";

// EMAIL JS
import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';

export default function FeedBackPopup(Props) {

  // Feedback popup
  const {FeedBackState, setFeedbackState} = useContext(FeedBackContext)

  const DirectUserToTele = () => {
    const Judul = document.getElementById('InputName').value
    const IsiText = document.getElementById('IsiTextArea').value

    if(!Judul) {
      alert("Harap isi terlebih dahulu")
      return;
    } else if (Judul === " "){
      alert("Harap isi terlebih dahulu")
      return;
    }
    
    if (!IsiText) {
      alert("Harap isi terlebih dahulu")
      return;
    } else if (IsiText === " "){
      alert("Harap isi terlebih dahulu")
      return;
    }

    const Pesan = `Tentang ${Judul}\n${IsiText}`;
    const EncodedPesan = encodeURIComponent(Pesan)
    window.open(`https://wa.me/6287888523299?text=${EncodedPesan}`, '_Blank')

    // Close Popup
    setTimeout(()=> {
      setFeedbackState(false)
    }, 3000)
  }

  const [isFocus, setIsFocus] = useState(false)
  function HandleFocusActive() {
    setIsFocus(true)
  }
  function HandleFocusDeActive() {
    setIsFocus(false)
  }

  const InputJudul = {
    width: "100%", height: "24px", marginTop: "-3px", marginBottom: "4px", padding:"4px 6px", fontFamily: "inter", outline: isFocus && ('none'), fontSize: "12px",
  }
  const InputIsiFeedback = {
    resize: "none", width: '100%', height: "72px", marginTop: "-3px", padding:"4px 6px", fontFamily: "inter",
    outline: isFocus && ('none'), fontSize: "12px"
  }

  // EMAIL JS
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm('service_ievsxpp', 'template_dath227', form.current, {
        publicKey: 'KZxOXlozFysvcLp__',
      })
      .then(
        () => {
          console.log('SUCCESS!');
        },
        (error) => {
          console.log('FAILED...', error.text);
        },
      );
  };
  

  return (
    <>
    <style>
        {`
            .floating-effect { 
                animation-name: floating;
                animation-duration: 3s;
                animation-iteration-count: infinite;
                animation-timing-function: ease-in-out;
            }

            @keyframes floating {
                0% { transform: translate(0, -8px); }
                50% { transform: translate(0, -2px); }
                100% { transform: translate(0, -8px); } 
            }
        `}
    </style>
      
        <div className="Comingsoon-continer" >
          {/* <div className="black-background" onClick={()=> {HandleShow();}} /> */}
          {Props.BlackBackground}
            <div className="main-comingsoon" style={{height: "fit-content"}}>
              <div style={{marginBottom: "4px"}} className="floating-effect">
                <img src="/image/Feed-back/Feedback.png" alt="" width={"120px"}/>
              </div>
              <div style={{}}>
                <div style={{textAlign: "center"}}>
                  <h4 style={{fontFamily: "inter", fontSize: "14px", marginBottom : "2px"}}>Need your feedback</h4>
                  <p style={{fontFamily: "inter", fontSize: "12px", opacity: "80%"}}>Leave your brilliant suggestions below</p>
                </div>

                <div style={{display: "flex", flexDirection: "column", gap: "8px", marginTop: "24px", marginBottom: "8px"}}>
                  <form ref={form} onSubmit={sendEmail}>
                    <label style={{fontSize: "12px", fontFamily: "inter"}} htmlFor="InputName">Nama kamu </label>
                    <input onFocus={HandleFocusActive} onBlur={HandleFocusDeActive} spellCheck={false} type="text" id="InputName" style={InputJudul} name="user_name"/> 
                    <label>Email</label>
                    <input type="email" name="user_email" />

                    <label style={{fontSize: "12px", fontFamily: "inter"}} htmlFor="InputName">Your feedback</label>
                    <textarea name="message" onFocus={HandleFocusActive} onBlur={HandleFocusDeActive} spellCheck={false} id="IsiTextArea" style={InputIsiFeedback}  ></textarea>
                    {/* <div style={{marginTop: '8px'}}> */}
                      <input type="submit" value="Send" className="button-coming" />
                    {/* </div> */}
                  </form>
                </div>
              </div>
          </div>
        </div>
    </>
  );
}