import { useContext } from "react"
import { useParams } from "react-router-dom"
import { useEffect } from "react"
import { useState } from "react"
import { ParamContext } from "../paramsContext"
import Error404 from "../../Component/Error404/Error404"
import BottomNav from "../../Component/Nav-comp/Bottom-nav/Bottom-nav"
import { DarkModeContext } from "../../DarkMode/Darkmode"
import { useNavigate } from "react-router-dom"

export default function Params() {

    // Darkmode
    const {modeToggle} = useContext(DarkModeContext)
    
    // const {id} = useParams()
    const {selectedData} = useContext(ParamContext)

    const ListStyle = {
		fontFamily: "inter",
		fontSize: "12px",
		whiteSpace: "pre-wrap",
		color: modeToggle ? "#999999" : "var(--SubHeading-shade)"
	}

	const BahanDiv = {
		marginTop: "12px",
		width: "100%",
		height: "fit-content",
		display: "flex",
		flexDirection: "column",
		gap: "8px",
		padding: "16px",
		borderRadius: "16px",
		backgroundColor: modeToggle ? "rgb(41, 41, 41)" : "var(--Ground-new-gray)",
		fontFamily: "inter"
	}

	const ImgPrev = {
		width: "100%",
		height: "150px",
		borderRadius: "8px"
	}

	
	// List Function
	const [CheckList] = useState([selectedData.BahanRecp])
	const Bahan = `Bahan untuk ${selectedData.NameRecp}`
	
	// Langkah-langkah
	const [HowToDo] = useState([selectedData.HowTo])
	const HowTo = `Langkah-langkah`

	// Source resep
	const [SourceRecipe] = useState([selectedData.SourceRecp])
	const Source = `Recipe by ${SourceRecipe}`

	// Navigate
	const Navigate = useNavigate()

  return (
    <div style={{ width: "100%", height: "100vh" }}>
      {selectedData && (
        <div style={{ padding: "16px", height: "fit-content", backgroundColor: modeToggle ? "black" : "white", paddingBottom: "64px"}}>
					{/* Back page */}
					<div onClick={()=> Navigate('/Explore')} style={{display: "flex", flexDirection: "row", gap: "12px", color: modeToggle ? "white" : "black", padding: "0px 0px 16px 0px", alignItems: "center", cursor: "pointer"}}>
						<i class="bi bi-arrow-left"></i> <p style={{fontSize: "14px", fontWeight: "600"}}>Back</p>
					</div>
					{/* Image */}
					<div style={{...ImgPrev, backgroundImage: `src(${selectedData.img})`}}>
						<img src={selectedData.img} alt="" width={"100%"} height={"100%"} style={{borderRadius: "8px", objectFit: "cover"}}/>
					</div>

					{/* Title */}
					<div style={{marginTop: "12px", fontFamily: "inter"}}>
          	<h3 style={{color: modeToggle ? "white" : "black", fontSize: "14px"}}>{selectedData.NameRecp}</h3>
          	<p style={{fontSize: "12px", color: modeToggle ? "#999999" : "var(--SubHeading-shade)", marginTop: "8px", marginBottom: "24px"}}>{selectedData.desc}</p>
					</div>

					{/* Bahan bahan */}
					<div style={BahanDiv}>
						<p style={{textAlign: "center", fontSize: "13px", fontWeight: "600", marginBottom: "0px", color: modeToggle ? "white" : "black"}}>{Bahan}</p>
						{CheckList.map((item, index) => 
							<p key={index} style={{...ListStyle, lineHeight: "20px"}}>{item}</p>
						)}
					</div>

					{/* Langkah langkah */}
					<div style={BahanDiv}>
						<p style={{textAlign: "center", fontSize: "13px", fontWeight: "600", marginBottom: "0px", color: modeToggle ? "white" : "black"}}>{HowTo}</p>
						{HowToDo.map((item, index) => 
							<p key={index} style={{...ListStyle, lineHeight: "20px"}}>{item}</p>
						)}
					</div>

					{/* Source */}
					<div style={{...ListStyle, marginTop: "12px", fontSize: "10px", textAlign: "center"}}>
						<p><i>{Source}</i></p>
					</div>

        </div>
      )}
      <BottomNav DarkMode={modeToggle} />
    </div>

    )
}