  const newLinkStyle = {
    textDecoration: "none",
    ...Props.NewWidth,
  }

  // Darkmode style area
  const HeaderDarkMode = {
    backgroundColor: Props.DarkMode ? "#000000" : 'var(--Ground-Black)',
    borderBottom: Props.DarkMode ? '1px solid #292929' : '0px',
  }

  const SearchBoxDarkMode = {
    ...Props.NewSearchBox,
    backgroundColor: 'var(--Shade-box)',
    color: " var(--Inner-Input)"

  }

  const SettingDarkMode = {
    ...Props.HiddenSetting,
    color: "var(--Inner-Input)",

  }

  // // Disable scroll if output from input is showing
  // useEffect(()=> {
