import { useEffect } from "react";
import { createContext, useState } from "react";

export const ParamContext = createContext()

export default function ParamProvider({children}) {
    const [getData, setData] = useState([]);
    const [ShowOutput, SetOutput] = useState();
    const [selectedData, setSelectedData] = useState(()=> {
        const SelectedItem = localStorage.getItem('SelectedData')
        return SelectedItem ? JSON.parse(SelectedItem) : null
    })
    const [DataMapInput, setDataMapInput] = useState([])
    const [MenuScroll, setMenuScroll] = useState([])
    const [selectedDataMenuScr, setSelectedDataMenuScr] = useState(()=> {
      const SelectedDataScroll = localStorage.getItem('DataMenuScroll')
      return SelectedDataScroll ? JSON.parse(SelectedDataScroll) : null
    })
    const [LoadingSkele, setLoadingSkele] = useState(true)
    useEffect(()=> {
        async function GetDataRecp() {
          try {
            const response = await fetch('https://668e29d6bf9912d4c92d0971.mockapi.io/api/ListRecp/ListRecp', {
              method: 'GET',
              headers: {'content-type':'application/json'},
            })
            if(!response.ok) {
              throw new Error(`Gagal mendapatkan API ${response.status}`);
            } else {
              const data = await response.json()
                setLoadingSkele(false)
                setMenuScroll(data)
                setDataMapInput(data)
            }
          } catch (error) {
            console.log(error)
          }
        }
          GetDataRecp()
        }, [setMenuScroll, setLoadingSkele])

    // GET FROM INPUT
    function SelectedDataItem(item) {
        setSelectedData(item); 
        localStorage.setItem("SelectedData", JSON.stringify(item));
    }

    // GET FROM MENUSCROLL
    function SelectedDataMenuScroll(item) {
      setSelectedDataMenuScr(item)
      localStorage.setItem("DataMenuScroll", JSON.stringify(item))
    }

    


    return (
        <ParamContext.Provider value={{SelectedDataMenuScroll, selectedDataMenuScr, setSelectedDataMenuScr, DataMapInput, setDataMapInput, LoadingSkele, setLoadingSkele, MenuScroll, setMenuScroll, getData, setData, ShowOutput, SetOutput,selectedData, setSelectedData, SelectedDataItem}}>
            {children}
        </ParamContext.Provider>
    )
}