import { useEffect } from "react";
import { createContext } from "react";
import { useState, } from "react";
import { useContext } from "react";
import { ParamContext } from "../paramsContext";

export const ParamsSavedMenuContext = createContext()

export default function ParamsProviderSave({children}) {
    const [StatusMenu, setStatusMenu] = useState(()=> {
        const SaveStatusMenu = localStorage.getItem('SaveStatusMenuParams')
        return SaveStatusMenu === "true"
    })

    // SAVE MENU
    const {selectedDataMenuScr, setSelectedDataMenuScr} = useContext(ParamContext);

    function StatusSaveParams(MenuSave) {
        setSelectedDataMenuScr((prev) => ({...prev, SaveMenu: !MenuSave}))
        localStorage.setItem('SaveStatusMenuParams', MenuSave)
            // setStatusMenu((prev)=> !prev)
            // Icon save
        }


    return (
        <ParamsSavedMenuContext.Provider value={{StatusMenu, setStatusMenu, StatusSaveParams}}>
            {children}
        </ParamsSavedMenuContext.Provider>

    )
}