// Props content
import ContentNewSect from "./Content-props/Content-props";

// Animate on scroll Library
import AOS from "aos";
import "aos/dist/aos.css";
import { useEffect, useState, useContext, useRef } from "react";

// Popup style
import '../Board-Home/Banner-cta.css'

// Darkmode
import { DarkModeContext } from "../../DarkMode/Darkmode";
import SaveMenuProvider from "./Content-props/SendSaveContext";

// Save Context
import { SaveContext } from "./Content-props/SendSaveContext";
import { useNavigate } from "react-router-dom";
import SavedPopup from "./Content-props/SavedPopup";

export default function NewestRecipeSect(Props) {
  useEffect(() => {
    AOS.init({ duration: 1000 });
  }, []);

  // Popup
  const [comingPopup, setComingPopup] = useState()
  const HandleShow = () => {
    setComingPopup((prev)=> !prev)
  }

  useEffect(()=> {
    comingPopup ? document.body.style.overflow="hidden" : document.body.style.overflow="auto";
  }, [comingPopup])


  // DarkMode 
  const [showOpenPopup, setShowOpenPopup] = useState(true)
  const HandleClosePopup = () => {
    setShowOpenPopup(false)
  }


  const {modeToggle, setModeToggle} = useContext(DarkModeContext)

  const {IsProps1, setIsProps1} = useContext(SaveContext)
  const {setIsPopupProps1} = useContext(SaveContext)
  const [IsProps1Local, setIsProps1Local] = useState(false)

  const {IsProps2, setIsProps2} = useContext(SaveContext)
  const {setIsPopupProps2} = useContext(SaveContext)
  const [IsProps2Local, setIsProps2Local] = useState(false)

  const {IsProps3, setIsProps3} = useContext(SaveContext)
  const {setIsPopupProps3} = useContext(SaveContext)
  const [IsProps3Local, setIsProps3Local] = useState(false)

  const {IsProps4, setIsProps4} = useContext(SaveContext)
  const {IsPopupProps4, setIsPopupProps4} = useContext(SaveContext)
  const [IsProps4Local, setIsProps4Local] = useState(false)

  // Save count menu
  const {IsMenuCount, setIsMenuCount} = useContext(SaveContext)

  // Props1 
  function HandleSaveProps1() {
    setIsProps1((prev)=> !prev)
    setIsPopupProps1((prev)=> !prev)
  }
  useEffect(()=> {
    if(IsProps1) {
      setIsProps1Local(true)
    } else {
      setIsProps1Local(false)
    }
  }, [IsProps1, setIsProps1Local])
  // !----------------------------------
  // Props2 
  function HandleSaveProps2() {
    setIsProps2((prev)=> !prev)
    setIsPopupProps2((prev)=> !prev)
  }
  // Props3 
  function HandleSaveProps3() {
    setIsProps3((prev)=> !prev)
    setIsPopupProps3((prev)=> !prev)
   
  }
  // Props4 
  function HandleSaveProps4() {
    setIsProps4((prev)=> !prev)
    setIsPopupProps4((prev)=> !prev)
  }


  return (
    <>
       {comingPopup && (
        <div className="Comingsoon-continer">
          <div className="black-background" onClick={HandleShow} />
            <div className="main-comingsoon">
              <div style={{marginBottom: "24px"}}>
                <img src="/image/imgComp/comingnew.png" alt="" width={"112px"}/>
              </div>
              <div style={{textAlign: "center"}}>
                  <h4 style={{fontFamily: "inter", fontSize: "14px", marginBottom : "2px"}}>In development stage</h4>
                  <p style={{fontFamily: "inter", fontSize: "12px", opacity: "80%"}}>Aishh shiball... Akan segera tersedia!</p>
              </div>
              <div style={{marginTop: '16px'}}>
                <button className="button-coming" onClick={HandleShow}>okay</button>
              </div>
          </div>
        </div>
    )}

    


    <div
    className="New-recipe-sect"
      // data-aos="fade-up"
      data-aos-delay="2000"
      data-aos-offset="50"
    >
      <div className="Wrappper-new-sect">
        {/* Top title section */}
        <div className="Top-new-sect">
          {/* Title section */}
          <div>
            <h1 style={{color: Props.DarkMode ? "white" : "black"}}>Resep terbaru buat kamu ✨</h1>
            <p style={{color: Props.DarkMode ? "var(--Inner-Input)" : "var(--SubHeading-shade)"}}>Inspirasi masakan baru dengan kumpulan resep yang menarik!</p>
          </div>
          {/* Button Section */}
          <div>
            <button onClick={()=> setComingPopup(true)}>Lihat semua</button>
          </div>
        </div>

        {/* Co  `ntent */}
        <div className="Content-new-sect">
          <div className="wrapper-content-newSect">
            <div style={{marginLeft: "-1.5px"}} >
              {modeToggle ? (
                  <ContentNewSect
                  DarkMode={modeToggle}

                  // Toggle Save
                  SavedProps1={(prev)=> !prev}
                  SaveBoxProps1={<div className="Save-coock-props" onClick={HandleSaveProps1} style={{backgroundColor: Props.DarkMode ? "var(--Shade-color)" : "var(--Shade-color)"}}>
                  {IsProps1 ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>}
                </div>}
                  OpenPopupSavedProps1={<SavedPopup />}
                  Src="/Image/Food-img/image 2img1.jpg"
                  Alt="Image of Recipe"
                  Title="Sup udang"
                  Bahan="5 Bahan"
                  Desc="Sup udang enak pokoknya coba aja dulu, pasti enak dan simpel gacor kang"
                  waktuMasak="36 Menit"
                  />
              ) : (
                  <ContentNewSect
                  DarkMode={modeToggle}

                  // Toggle Save
                  SavedProps1={(prev)=> !prev}
                  SaveBoxProps1={<div className="Save-coock-props" onClick={HandleSaveProps1} style={{backgroundColor: Props.DarkMode ? "var(--Shade-color)" : "var(--Shade-color)"}}>
                  {IsProps1 ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>}
                </div>}
                  OpenPopupSavedProps1={<SavedPopup />}


                  Src="/Image/Food-img/image 2img1.jpg"
                  Alt="Image of Recipe"
                  Title="Sup udang"
                  Bahan="5 Bahan"
                  Desc="Sup udang enak pokoknya coba aja dulu, pasti enak dan simpel gacor kang"
                  waktuMasak="36 Menit"
                  />
              )}
            </div>

            <div>
              {modeToggle ? (
                  <ContentNewSect
                  DarkMode={modeToggle}
                  // Toggle Save
                  SavedProps2={(prev)=> !prev}
                  SaveBoxProps2={<div className="Save-coock-props" onClick={HandleSaveProps2} style={{backgroundColor: Props.DarkMode ? "var(--Shade-color)" : "var(--Shade-color)"}}>
                  {IsProps2 ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>}
                </div>}
                  OpenPopupSavedProps2={<SavedPopup />}


                  Src="/Image/Food-img/Image 3.jpg"
                  Alt="Image of Recipe"
                  Title="Serundeng"
                  Bahan="7 Bahan"
                  Desc="Sup udang enak pokoknya coba aja dulu, pasti enak dan simpel gacor kang"
                  waktuMasak="24 Menit"
                  />
              ) : (
                  <ContentNewSect
                  DarkMode={modeToggle}
                  SavedProps2={(prev)=> !prev}
                  SaveBoxProps2={<div className="Save-coock-props" onClick={HandleSaveProps2} style={{backgroundColor: Props.DarkMode ? "var(--Shade-color)" : "var(--Shade-color)"}}>
                  {IsProps2 ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>}
                </div>}
                  OpenPopupSavedProps2={<SavedPopup />}


                  Src="/Image/Food-img/Image 3.jpg"
                  Alt="Image of Recipe"
                  Title="Serundeng"
                  Bahan="7 Bahan"
                  Desc="Sup udang enak pokoknya coba aja dulu, pasti enak dan simpel gacor kang"
                  waktuMasak="24 Menit"
                  />
              )}
            </div>

            <div>
              {modeToggle ? (
                <ContentNewSect
                  DarkMode={modeToggle}
                  SavedProps3={(prev)=> !prev}
                  SaveBoxProps3={<div className="Save-coock-props" onClick={HandleSaveProps3} style={{backgroundColor: Props.DarkMode ? "var(--Shade-color)" : "var(--Shade-color)"}}>
                  {IsProps3 ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>}
                </div>}
                  OpenPopupSavedProps3={<SavedPopup />}

                  Src="/Image/Banner.png"
                  Alt="Image of Recipe"
                  Title="Acikiwiw"
                  Bahan="9 Bahan"
                  Desc="Sup udang enak pokoknya coba aja dulu, pasti enak dan simpel gacor kang"
                />
              ) : (
                <ContentNewSect
                  DarkMode={modeToggle}
                  SavedProps3={(prev)=> !prev}
                  SaveBoxProps3={<div className="Save-coock-props" onClick={HandleSaveProps3} style={{backgroundColor: Props.DarkMode ? "var(--Shade-color)" : "var(--Shade-color)"}}>
                  {IsProps3 ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>}
                </div>}
                  OpenPopupSavedProps3={<SavedPopup />}

                  Src="/Image/Banner.png"
                  Alt="Image of Recipe"
                  Title="Acikiwiw"
                  Bahan="9 Bahan"
                  Desc="Sup udang enak pokoknya coba aja dulu, pasti enak dan simpel gacor kang"
                />
              )}
            </div>

            <div>
              {modeToggle ? (
                  <ContentNewSect
                  DarkMode={modeToggle}
                  SavedProps4={(prev)=> !prev}
                  SaveBoxProps4={<div className="Save-coock-props" onClick={HandleSaveProps4} style={{backgroundColor: Props.DarkMode ? "var(--Shade-color)" : "var(--Shade-color)"}}>
                  {IsProps4 ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>}
                </div>}
                  OpenPopupSavedProps4={<SavedPopup />}

                  Src="/Image/Banner.png"
                  Alt="Image of Recipe"
                  Title="Slemehoy"
                  Bahan="12 Bahan"
                  Desc="Sup udang enak pokoknya coba aja dulu, pasti enak dan simpel gacor kang"
                />
              ) : (
                <ContentNewSect
                  SavedProps4={(prev)=> !prev}
                  SaveBoxProps4={<div className="Save-coock-props" onClick={HandleSaveProps4} style={{backgroundColor: Props.DarkMode ? "var(--Shade-color)" : "var(--Shade-color)"}}>
                  {IsProps4 ? <i className="fa-solid fa-heart"></i> : <i className="fa-regular fa-heart"></i>}
                </div>}
                  OpenPopupSavedProps4={<SavedPopup />}

                  Src="/Image/Banner.png"
                  Alt="Image of Recipe"
                  Title="Slemehoy"
                  Bahan="12 Bahan"
                  Desc="Sup udang enak pokoknya coba aja dulu, pasti enak dan simpel gacor kang"
                />
              )}
            </div>

          </div>
        </div>
      </div>
    </div>
    </>
  );
}
