import { ParamContext } from "../../NewPageCozDataMap/paramsContext";
import { useContext } from "react";

import { DarkModeContext } from "../../DarkMode/Darkmode";
import { useNavigate } from "react-router-dom";

export default function RecipeScrollContent(Props) {
  const PropsStyle = {
    minWidth: "328px",
    height: "90px",
    gap: "16px",
    padding: Props.RemovePaddingInSavedSect ? "0px" : "0px 16px",
    marginTop: "16px",
    cursor: "pointer",

    display: "flex",
    justifyContent: "space-between",
    flexDirection: "row",
  };

  const descStyle = {
    fontFamily: "inter",
    fontWeight: "400",
    fontSize: "10px",
    color: Props.DarkMode ? "var(--Inner-Input)" : "#666666",
  };

  // Event Handler
  function HandleClick() {
    if (Props.onClick) {
      Props.onClick();
    }
  }

  // Darkmode
  const { modeToggle, setModeToggle } = useContext(DarkModeContext);

  return (
    <div style={PropsStyle} onClick={HandleClick}>
      {/* Image section */}
      <div style={{width: "140px", height: "90px", borderRadius: "8px", backgroundColor: Props.DarkMode ? "rgb(41, 41, 41)" : "#ebebeb",flex: "0 0 auto",}}> {Props.Img} </div>
      
      {/* Title desc section */}
      <div style={{display: "flex", flexDirection: "column", justifyContent: "center", width: "100%",}}>

        <h1 style={{fontSize: "14px",fontWeight: "700", fontFamily: "Main-Bold", marginBottom: "2px",color: Props.DarkMode ? "white" : "black",}}> {Props.Title}</h1>

        <p style={{fontSize: "10px",fontFamily: "inter",fontWeight: "400", marginBottom: "8px", color: Props.DarkMode ? "var(--Inner-Input)" : "#666666",}}> {Props.Desc} </p>

        <div style={{ display: "flex", flexDirection: "row" }}> 
          <div style={{ display: "flex", alignItems: "center", gap: "4px" }}>
            {Props.DarkMode ? (
              <img src="/Icons/Document-Darkmode.svg" alt="Doc-ic" style={{ marginLeft: "-2px", width: "15px"}}/>
            ) : (
              <img src="/Icons/Document.svg" alt="Doc-ic"style={{ marginLeft: "-2px", width: "15px" }}/>
            )}
            <p style={descStyle}>{Props.Bahan}</p>
          </div>
        </div>
      </div>

      {/* Arrow icon */}
      <div style={{ display: "flex", alignItems: "center" }}>
        {modeToggle ? (
          <img src="/Icons/Right_Arrow_Darkmode.svg" alt="RightArrow" />
        ) : (
          <img src="/Icons/Right_Arrow.svg" alt="RightArrow" />
        )}
      </div>
    </div>
  );
}
