import BottomNav from "../Nav-comp/Bottom-nav/Bottom-nav"
import { useContext, useEffect } from "react"
import { useNavigate } from "react-router-dom"

import { InputContext } from "../Nav-comp/Top-nav/SendRef"

// Darkmode Context
import { DarkModeContext } from "../../DarkMode/Darkmode"

export default function Error404(Props) {
    // Darkmode Toggle
    const {modeToggle, setModeToggle} = useContext(DarkModeContext)
    
    // Navigate
    const Navigate = useNavigate()

    // Blur input in explore
    const {setStateInputRef} = useContext(InputContext)
    useEffect(()=> {
        setStateInputRef(false)
    }, [setStateInputRef])

    return (
        <>
            <style>
                {`
                    .error-container {
                    width: 100%;
                    height: 100vh;
                    
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    flex-direction: column;
                    gap: 32px;

                    }
                
                `}
            </style>
            {modeToggle ? (
                <>
                    <div className="error-container" style={{backgroundColor: "black"}}>
                        <img src="/Image/imgComp/404orange.png" alt="" width={"220px"} />
                        <div style={{textAlign: "center"}}>
                            <p style={{fontFamily: "Main-Bold", color: modeToggle ? "white" : "black"}}>Page not found!</p>
                            <p style={{fontFamily: "inter", fontSize: '12px', fontWeight: "400", color: modeToggle ? "var(--Inner-Input)" : ""}}>Turn back into previous pages</p>

                            <p onClick={()=> Navigate('/Home')} style={{fontFamily: "inter", marginTop: "16px",fontSize: '12px', fontWeight: "400", textDecoration: "underline", cursor: "pointer", color: modeToggle ? "#007bff" : "#007bff"}} >Back</p>
                        </div>
                    </div>
                    {/* {modeToggle ? (
                        <BottomNav DarkMode={modeToggle} />
                    ) : (
                        <BottomNav />
                    )} */}
                </>
            ) : (
                <>
                    <div className="error-container">
                            <img src="/Image/imgComp/404orange.png" alt="" width={"220px"} />
                            <div style={{textAlign: "center"}}>
                                <p style={{fontFamily: "Main-Bold"}}>Page not found!</p>
                                <p style={{fontFamily: "inter", fontSize: '12px', fontWeight: "400"}}>Turn back into previous pages</p>
                                <p onClick={()=> Navigate('/Home')} style={{fontFamily: "inter", marginTop: "16px",fontSize: '12px', fontWeight: "400", textDecoration: "underline", cursor: "pointer", color: modeToggle ? "#007bff" : "#007bff"}} >Back</p>
            
                            </div>
                        </div>
                        {/* {modeToggle ? (
                            <>
                            <BottomNav DarkMode={modeToggle} />
                            </>
                        ) : (
                            <BottomNav />
                        )} */}
                </>
            )}
        </>
    )
}