import { useContext } from "react"
import { DarkModeContext } from "../../../DarkMode/Darkmode"
import { useNavigate } from "react-router-dom"

export default function SavedPopup() {
    const {modeToggle, setModeToggle} = useContext(DarkModeContext)
    const NavigateToSaved = useNavigate()

    return (
        <div>
            {modeToggle ? (
            <div onClick={()=> NavigateToSaved('/Saved')} className="Popup-Saved-Success" data-aos="fade-up" style={{backgroundColor: "#111111", color: "white", outline: "1px solid #ffffff40"}}>
                <h1>Menu berhasil disimpan!</h1>
                <p>Lihat disini</p>
            </div>
          ) : (
            <div onClick={()=> NavigateToSaved('/Saved')} className="Popup-Saved-Success" data-aos="fade-up" style={{backgroundColor: "white", color: "black"}}>
                <h1>Menu berhasil disimpan!</h1>
                <p>Lihat disini</p>
            </div>
          )}
        </div>
    )
}