import { createContext, useState, useRef } from "react";

export const InputContext = createContext()

export function InputSearchProvider({children}) {
    // State for active input in Explore
    const [StateInputRef, setStateInputRef] = useState(false) 
    const InputRefFocus = useRef(null)

    // State for remove input Header in explore
    const [RemoveInputHeader, setRemoveInputHeader] = useState(true)

    return (
        <InputContext.Provider value={{InputRefFocus, StateInputRef, setStateInputRef, RemoveInputHeader, setRemoveInputHeader}}>
            {children}
        </InputContext.Provider>
    )
}